import React, { useEffect, useState, useRef } from 'react';
import logo from './logo.svg';
import Styled from "./App.styles";
import Web3 from "web3";
import ReactJson from 'react-json-view'
import _ from 'lodash';

// axieContract: new l.AxieContract(t.axie, e),
//     landContract: new l.LandContract(t.land, e),
//         itemContract: new l.ItemCollectionContract(t.item, e),
//             exchangeContract: new l.ExchangeContract(t.exchange, e),
//                 clockAuctionContract: new l.ClockAuctionContract(t.clockAuction, e),
//                     offerAuctionContract: new l.OfferAuctionContract(t.offerAuction, e),
//                         roninWETHContract: new l.RoninWETHContract(t.sideChainWeth, e),
//                             roninSLPContract: new l.BreedingPotionContract(t.sidechainSLP, e),
//                                 roninAXSContract: new u.ERC20Contract(t.sidechainAXS, e),
//                                     roninRewardDistributor: new c.RewardDistributorContract(t.rewardDistributor, e),
//                                         sidechainGatewayContract: new h.SidechainGatewayManagerContract(t.sidechainGatewayManager, e)


const App = (props: any) => {
    const [rpcJson, setRpcJson] = useState({});
    const [abiData, setAbiData] = useState([]);
    const [contractSelected, setContractSelected] = useState<any>('');
    const [methodSelected, setMethodSelected] = useState<any>('');

    const [inputArray, setInputArray] = useState<any>([]);
    const [inputValue, setInputValue] = useState<any>([]);

    const web3 = useRef<any>(null);
    const mainContract = useRef<any>(null);
    const contractAddress = useRef<any>({
        mainchainGatewayManager: "0x1a2a1c938ce3ec39b6d47113c7955baa9dd454f2",
        sidechainGatewayManager: "0xe35d62ebe18413d96ca2a2f7cf215bb21a406b4b",
        roninWeth: "0xc99a6a985ed2cac1ef41640596c5a5f9f4e19ef5",
        roninAXS: "0x97a9107c1793bc407d6f527b77e7fff4d812bece",
        roninSLP: "0xa8754b9fa15fc18bb59458815510e40a12cd2014",
        axie: "0x32950db2a7164ae833121501c797d79e7b79d74c",
        aec: "0x173a2d4fa585a63acd02c107d57f932be0a71bcc",
        land: "0x8c811e3c958e190f5ec15fb376533a3398620500",
        item: "0xa96660f0e4a3e9bc7388925d245a6d4d79e21259",
        rewardDistributor: "0x1a35e7ed2a2476129a32612644c8426bf8e8730c",
        exchange: "0x2da06d60bd413bcbb6586430857433bd9d3a4be4",
        clockAuction: "0x213073989821f738a7ba3520c3d31a1f9ad31bbd",
        offerAuction: "0x5b16d12a0c2c88db94115968abd7afa78b6bc504"
    });

    useEffect(() => {
        // https://ronin-testnet.skymavis.com/rpc
        web3.current = new Web3('https://api.roninchain.com/rpc');
    }, []);

    const test = async () => {
        var tmpInputValue: any = _.clone(inputValue);
        tmpInputValue.map((item: any, key: any) => {
            if (inputArray[key].type == 'uint256') {
                return web3.current.eth.abi.encodeParameter('uint256', item);
            }
            
        })

        try {
            var res = await mainContract.current.methods[methodSelected].apply(null, tmpInputValue).call();
            if (typeof res === 'string') {
                setRpcJson({
                    value: res
                })
            }
            else if (typeof res === 'object') {
                setRpcJson(res)
            }
        } catch (error: any) {
            setRpcJson({
                error: error.message
            })
        }
    }

    const onContractChange = (evt: any) => {
        var abi = require(`./abi/${evt.target.value}.json`);
        setContractSelected(evt.target.value);
        setAbiData(abi);
        mainContract.current = new web3.current.eth.Contract(abi, contractAddress.current[evt.target.value]);
    }

    const onMethodChange = (evt: any) => {
        setMethodSelected(evt.target.value);
        var data: any = _.find(abiData, { name: evt.target.value });
        setInputArray(data.inputs)

        var inputs: any = [];
        data.inputs.map((item: any) => {
            inputs.push('');
        })
        setInputValue(inputs);
        setRpcJson({})
    }

    const onValueChange = (evt: any, index: any) => {
        var tmpInputValue: any = _.clone(inputValue);
        tmpInputValue[index] = evt.target.value;
        setInputValue(tmpInputValue);
    }

    return (
        <>
            <div style={{ marginBottom: '15px' }}>
                <div>Contract</div>
                <select onChange={onContractChange} value={contractSelected}>
                    <option key="none" value="">請選擇</option>
                    {
                        ['axie', 'land', 'item', 'exchange', 'clockAuction', 'offerAuction', 'roninAXS', 'roninSLP', 'roninWETH'].map((item: any, key: any) => {
                            return (<option key={key} value={item}>{item}</option>)
                        })
                    }
                </select>
            </div>
            <div style={{ marginBottom: '15px'}}>
                <div>Mathod</div>
                <select onChange={onMethodChange} value={methodSelected}>
                    <option key="none" value="">請選擇</option>
                    {
                        _.filter(abiData, {type: 'function'}).map((item: any, key: any) => {
                            return (<option key={key} value={item.name}>{item.name}</option>)
                        })
                    }
                </select>
            </div>
            <div style={{ marginBottom: '15px' }}>
                {
                    JSON.stringify(inputValue[1])
                }
                {
                    inputArray.map((item: any, key: any) => {
                        return <div key={key} style={{ marginBottom: '15px'}}>
                            <div>name: {item.name}</div>
                            <div>type: {item.type}</div>
                            <div><input onChange={(evt) => { onValueChange(evt, key)}} value={inputValue[key]}/></div>
                        </div>
                    })
                }
            </div>
            <div style={{ marginBottom: '15px' }}>
                <button onClick={test}>
                    send
                </button>
            </div>
            <ReactJson src={rpcJson} />
        </>
    );
}

export default App;